.header {
    margin: 15.63vw 0 9.38vw;
    background-size: 100%;
}

.header__branding {
    display: -ms-flexbox;
    display: flex;
}

.header__logo,
.header__logo:link,
.header__logo:visited,
.header__logo:active {
    background-color: #ffffff;
    color: #000000;
    padding: 5.63vw;
    font-size: 5.63vw;
    display: inline-block;
    font-weight: 900;
    text-transform: uppercase;
    text-decoration: none;
    width: 35.63vw;
    margin-right: 6.25vw;
}

.header__description {
    letter-spacing: 1.56vw;
    text-transform: uppercase;
    font: 3.75vw sans-serif;
    color: #ffffff;
    margin-top: 5.63vw;
}

.header__twitter-button {
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 15.63vw 0 0;
    -ms-flex-direction: column;
        flex-direction: column;
}

.header__twitter-button__description {
    color: #ffffff;
    font-size: 4.38vw;
}

@media (min-width: 750px) {
    .header {
        margin: 50px 0 30px;
        min-height: 120px;
        background-size: 100%;
    }

    .header__logo,
    .header__logo:link,
    .header__logo:visited,
    .header__logo:active {
        width: auto;
        padding: 18px;
        font-size: 25px;
        margin-right: 20px;
    }

    .header__description {
        letter-spacing: 5px;
        font-size: 16px;
        margin-top: 20px;
    }

    .header__twitter-button {
        text-align: right;
        -ms-flex-pack: end;
            justify-content: flex-end;
        -ms-flex-align: end;
            align-items: flex-end;
        margin: auto;
    }

    .header__twitter-button__description {
        font-size: 14px;
    }
}



